*,
*::after,
*::before {
  box-sizing: border-box; }

.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  clip: rect(0, 0, 0, 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

.container {
  padding: 0 110px; }

@media screen and (max-width: 767px) {
  .container {
    padding: 0 16px; } }

.d-flex {
  display: flex; }

.list {
  list-style: none;
  margin: 0;
  padding: 0; }

.body {
  background-color: white;
  display: flex;
  flex-direction: column;
  font-family: Arial, "Helvetica CY", "Nimbus Sans L", sans-serif;
  height: 100vh;
  max-width: 1366px;
  margin: 0 auto;
  min-width: 320px;
  width: 100%; }

.body > * {
  flex-shrink: 0; }

.header-container {
  padding-right: 114px; }

.header {
  height: 48px;
  left: 0;
  position: absolute;
  top: 32px;
  width: 100%; }

.header__logo {
  height: 48px;
  left: 50%;
  position: absolute;
  top: 0;
  transform: translateX(-50%);
  width: 137px; }

.header__contacts {
  align-items: center;
  height: 100%;
  justify-content: space-between;
  width: 100%; }

@media screen and (max-width: 767px) {
  .header-container {
    padding-right: 7px; } }
